.burger {
    height: 3em;
    width: 3em;
    position: relative;
    font-size: 12px;
    cursor: pointer;
    margin-left: 5%;
    margin-top: 2%;
 }
    .burger .burgerlines:after {
      left: 0;
      top: -1em; }
    .burger .burgerlines:before {
      left: 1em;
      top: 1em; }
    .burger:after {
      content: '';
      display: block;
      position: absolute;
      height: 150%;
      width: 150%;
      top: -25%;
      left: -25%; }
    .burger .burgerlines {
      top: 50%;
      margin-top: -0.125em; }
      .burger .burgerlines, .burger .burgerlines:after, .burger .burgerlines:before {
        display: block;
        content: '';
        width: 100%;
        border-radius: 0.25em;
        background-color: black;
        height: 0.25em;
        position: absolute;
}


.sidenavContainer .option
{
  border-bottom: 1px solid #E8E8E8;
}

.sidenavContainer :last-child
{
  border-bottom: none;
}
.sidenavContainer nav :first-child
{
  padding-top: 0;
}


.sidenavContainer a
{
    font-family: 'Whitney-Book';
    font-weight: lighter;
    letter-spacing: 0.1rem;
    text-transform: uppercase; 
    padding: 0 1rem;
    color: black;
    opacity: 0.8;
    font-size: 1.2rem; 
    text-decoration: none;
}

@media only screen and (min-width : 320px) and (max-width: 1024px)
{
  .burger {
    font-size: 8px;
  }

  .sidenavContainer nav
  {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding-top: 5rem;
  }

  .sidenavContainer .option
  {
    padding-bottom: 2.5rem;
    padding-top: 2.5rem;

  }
}